import React, { useState, useEffect, useRef } from 'react';

import SquareText from '../SquareText';
import Highlighter from '../Highlighter';

const randomInteger = (b, e) => Math.floor(Math.random()*e) + b;
const randomSquare = () => `${String.fromCharCode(64 + randomInteger(1, 8)).toLowerCase()}${randomInteger(1, 8)}`;

const Type = props => {
	
	const [currentSquare, setCurrentSquare] = useState(randomSquare());
  	const [key, setKey] = useState("");
  	const [input, setInput] = useState("");
	const [exitCounter, setExitCounter] = useState(0);

  	const usedConstructor = useRef(false);
  	useEffect(() => {
    	if(usedConstructor.current) return;
   		
		document.addEventListener("keydown", e => setKey(e.key));
    	usedConstructor.current = true;

  	}, []);

	useEffect(() => {
		if(input == currentSquare) setCurrentSquare(randomSquare());
	}, [input]);
	
	useEffect(() => {

		if(!key) return;
		
		const charCode = key.charCodeAt(0);

		if(key === "Backspace") { 
			setInput(i => i.slice(0, -1)); 
			setKey(""); 
		} else if(input.length == 2 && (charCode >= 97 && charCode <= 104)) 
			setInput(key)
		else if(!(input.length == 2 || (input.length == 0 && (charCode < 97 || charCode > 104))
				|| (input.length == 1 && (charCode < 49 || charCode > 56)))) 
			setInput(i => i + key);
		
  	}, [key]);

	useEffect(() => setKey(props.virtualKey), [props.virtualKey]);

	useEffect(() => {
		if(exitCounter == 2) props.onExit();
	}, [exitCounter])

  return (
    <React.Fragment>
		<Highlighter chessBoardSize={props.chessBoardSize} onExit={() => setExitCounter(c => c + 1)} willExit={props.willExit} square={currentSquare} flip={props.flip} />
     	<SquareText disableFade={true} onExit={() => setExitCounter(c => c + 1)} willExit={props.willExit}>{input}</SquareText>  
    </React.Fragment>
  );
}

export default Type;