import React, { useState, useEffect } from 'react';
import { useSpring, animated } from 'react-spring'

const SquareText = props => {

    const [square, setSquare] = useState(props.children);

    const [styles, api] = useSpring(() => ({
        to: { opacity: 1 }, from: { opacity: 0 }
    }));

    useEffect(() => {
        if(props.disableFade) {
            setSquare(props.children); 
            return;
        }

        if(square !== props.children) {
            api.start({ to: { opacity: 0 } });
            setTimeout(() => setSquare(props.children), 400);
        } else 
            api.start({ to: { opacity:1 } });

    }, [props.children, square, api, props.disableFade]);

    useEffect(() => {
        if(props.willExit)
            api.start({ to: { opacity: 0 }, onRest: props.onExit });
    }, [props.willExit, api, props.onExit])

    return (
        <div className={"squareTextContainer"}>
            <animated.div style={styles}>
                <h1 className={"squareText"}>{square}</h1>
            </animated.div>
        </div>
    );
}

export default SquareText;