import React, { useEffect, useState } from 'react';

import SquareText from '../SquareText';

const randomInteger = (b, e) => Math.floor(Math.random()*e) + b;
const randomSquare = () => `${String.fromCharCode(64 + randomInteger(1, 8)).toLowerCase()}${randomInteger(1, 8)}`;

const Click = props => {

  const [currentSquare, setCurrentSquare] = useState(randomSquare());

  useEffect(() => {
    if(props.clickedSquare && props.clickedSquare === currentSquare)
      setCurrentSquare(() => randomSquare());

  }, [props.clickedSquare])

  return (
    <SquareText onExit={props.onExit} willExit={props.willExit}>{currentSquare}</SquareText>  
  );
}

export default Click;