import { Stage, Layer, Rect } from 'react-konva';
import React, { useState, useEffect, useRef, useCallback } from 'react';
import Konva from 'konva';

const Highlighter = props => {
	
	const [backdropRect, setBackdropRect] = useState(null);
	const [highlightRect, setHighlightRect] = useState(null);

	const usedConstructor = useRef(false);

	const getFileAndRank = useCallback(() => {
		return [props.flip ? 7 - (props.square.charCodeAt(0) - 97) : props.square.charCodeAt(0) - 97, 
				props.flip ? Number(props.square[1]) - 1 : 8 - Number(props.square[1])];
	}, [props.flip, props.square]);

	const fadeTween = (node, opacity, props) => { 
		return new Konva.Tween({
			node,
			opacity,
			easing: Konva.Easings.EaseInOut,
			...props
		});
	};

  	useEffect(() => {

		if(!backdropRect || !highlightRect || !highlightRect.parent || !backdropRect.parent) return;

		const [file, rank] = getFileAndRank();

		if(!usedConstructor.current) {

			highlightRect.x(file * props.chessBoardSize/8);
			highlightRect.y(rank * props.chessBoardSize/8);

			const fadeHighlightRect = fadeTween(highlightRect, 1, { duration: 0.4 }),
				  fadeBackdrop = fadeTween(backdropRect, 1, { duration: 0.2, onFinish: () => fadeHighlightRect.play() });

			fadeBackdrop.play();

		} else {

    		const moveHighlightRect = new Konva.Tween({
      			node: highlightRect,
      			duration: 0.4,
      			easing: Konva.Easings.EaseInOut,
      			x: (props.chessBoardSize/8) * file,
      			y: (props.chessBoardSize/8) * rank
    		});
    
    		moveHighlightRect.play();
		}

		usedConstructor.current = true;

  	}, [props.square, props.flip, getFileAndRank, backdropRect, highlightRect, props.chessBoardSize]);

	useEffect(() => {

		if(props.willExit && backdropRect && highlightRect && highlightRect.parent && backdropRect.parent) {

			const fadeBackdrop = fadeTween(backdropRect, 0, { duration: 0.4, onFinish: props.onExit }),
			fadeHighlightRect = fadeTween(highlightRect, 0, { duration: 0.2, onFinish: () => fadeBackdrop.play() });

	  		fadeHighlightRect.play();
		}

	}, [props.willExit, backdropRect, highlightRect, props.onExit])

  	return (
    	<Stage width={props.chessBoardSize} height={props.chessBoardSize}>
      		<Layer>
        		<Rect ref={r => setBackdropRect(r)} width={props.chessBoardSize} height={props.chessBoardSize} fill="rgba(0, 0, 0, 0.7)" opacity={0} />
        		<Rect ref={r => setHighlightRect(r)} width={props.chessBoardSize/8} height={props.chessBoardSize/8} fill="rgba(255, 255, 255, 0.7)" opacity={0} />
      		</Layer>
    	</Stage>
  	);
}

export default Highlighter;