import { useEffect, useState, useRef } from 'react';
import parse from 'ndjson-parse';
import delay from 'delay';

const ANIMATION_DELAY = 400;

const useLichess = (styleDiv, interval = 3000) => {
    
    const shouldUseEffect = useRef(true);
    
    const [position, setPosition] = useState({ d: { fen: ""}, t: "featured" });
    const lastPositionType = useRef("featured");

    const getReader = res => {
        const reader      = res.body.getReader();
        const utf8decoder = new TextDecoder("utf-8");

        return () => reader.read().then(chunk => parse(utf8decoder.decode(chunk.value, { stream: true }))); 
    }

    useEffect(() => {

        if(!shouldUseEffect.current) return;

        fetch("https://lichess.org/api/tv/feed").then(async res => {
            const read = getReader(res);

            for(;;) {

                const positions = await read();

                for(let i = 0; i < positions.length; ++i) {

                    if(positions[i].t == "featured" && lastPositionType.current == "fen") {
                        styleDiv.current.classList.remove("opacity");
                        await delay(interval + ANIMATION_DELAY);
                    }

                    setPosition({ ...positions[i] });
                    await delay(interval);
                }
            }
        });

        shouldUseEffect.current = false;

    }, []);

    useEffect(() => {

        if(position.t !== lastPositionType.current) 
            setTimeout(() => styleDiv.current.classList.add("opacity"), ANIMATION_DELAY);
        
        lastPositionType.current = position.t;

    }, [position]);

    return position.d.fen;
};

export default useLichess;