import React, { useEffect, useState, useRef } from 'react';

import Checkbox from '@mui/material/Checkbox';
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import FlipIcon from '@mui/icons-material/Flip';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';

const formControlLabelStyle = {
	color: "primary.alternative", 
	margin: 0,
	paddingTop: "0.5em"
};

const Settings = props => {
	return (
		<FormGroup sx={{
			flexDirection: "row",
			justifyContent: "space-evenly",
		}}>
			<FormControlLabel sx={{ ...formControlLabelStyle, width: "145.6px" }} control={<Checkbox sx={{color: "primary.main"}} checked={props.showNotation} onChange={props.onShowNotationChange} icon={<VisibilityOffIcon />} checkedIcon={<VisibilityIcon />} />} label={`${props.showNotation ? "Hide" : "Show"} notation`}/> 
			<FormControlLabel sx={{ ...formControlLabelStyle }} control={<Checkbox sx={{color: "primary.main"}} checked={props.flip} onChange={props.onFlipChange} icon={<FlipIcon />} checkedIcon={<FlipIcon />} />} label="Flip board"/> 
		</FormGroup>
	);
}

export default Settings;