import React, { useState } from "react";

import MenuIcon from '@mui/icons-material/Menu';
import CheckIcon from '@mui/icons-material/Check';

import Menu from './Menu';
import IconButton from './IconButton';
import MenuItem from './MenuItem'

import Games from '../Games';

import { gameIcon, checkIcon, iconButtonDesktop, iconButtonMobile } from './style';

const GameMenu = props => {

	const [anchor, setAnchor] = useState(null);

    return (
		<React.Fragment>
			<IconButton sx={props.mediaQuery ? iconButtonDesktop : iconButtonMobile } onClick={e => setAnchor(e.currentTarget)}><MenuIcon/></IconButton>
			<Menu anchorEl={anchor} open={Boolean(anchor)} onClose={() => setAnchor(null)}>
				{Object.keys(Games).map((gameName, i) => {
					const GameIcon = Games[gameName].Icon;
					return (
						<MenuItem key={i} onClick={() => props.onClick ? props.onClick(gameName) : null}>
							<GameIcon sx={gameIcon} color="primary" fontSize="large" /> 
							{gameName} 
							{props.checked === gameName && <CheckIcon sx={checkIcon} color="primary" fontSize="small" />}
						</MenuItem>
					);
				})}
			</Menu>
		</React.Fragment>  
	);
}

export default GameMenu;