import React, { useEffect, useState } from 'react';
import { useWindowWidth } from '@react-hook/window-size';

import GameMenu from './GameMenu';

import Games from './Games';
import SettingsDesktop from './SettingsDesktop';
import SettingsMobile from './SettingsMobile';
import BackgroundChessboard from './BackgroundChessboard';

import VirtualKeyboard from './VirtualKeyboard';

import useMediaQuery from '@mui/material/useMediaQuery';

const App = () => {

	const [gameName, setGameName] 		      = useState(Games.Click.name),
		  [newGameName, setNewGameName]       = useState(Games.Click.name),
		  [checked, setChecked]               = useState(Games.Click.name),
		  [clickedSquare, setClickedSquare]   = useState(null),
		  [showNotation, setShowNotation]     = useState(true),
		  [virtualKey, setVirtualKey] 		  = useState(null),
		  [exitCount, setExitCount]           = useState(0),
  		  [flip, setFlip] 				      = useState(false);

	const width = useWindowWidth();
	const mediaQuery = useMediaQuery("(min-width:560px)");

	useEffect(() => {
		if(gameName === Games.Click.name && exitCount === 1) {
			setGameName(newGameName); 
			setExitCount(0);
		}
		else if(gameName === Games.Type.name && ((mediaQuery && exitCount === 1) || (!mediaQuery && exitCount === 2))) {
			setGameName(newGameName); 
			setExitCount(0);
		}
	}, [exitCount, gameName, mediaQuery, newGameName]);

	useEffect(() => { if(newGameName === gameName) setNewGameName(checked); }, [checked, gameName, newGameName]);
	useEffect(() => { if(checked !== gameName) setNewGameName(checked); }, [gameName, checked]);

	useEffect(() => {

		const body = document.querySelector("body");

		if(mediaQuery) 
			body.style.cssText = `	
				height: 100%;
				display: flex;
				align-items: center;
				justify-content: center;
			`;
		else body.style.cssText = ``;

	}, [mediaQuery]);

	return (
		<React.Fragment>
			<div className="nav" >
				<GameMenu mediaQuery={mediaQuery} checked={checked} onClick={setChecked} />
				{!mediaQuery && <SettingsMobile onFlipChange={() => setFlip(!flip)} onShowNotationChange={() => setShowNotation(!showNotation)} flip={flip} showNotation={showNotation} /> }
			</div>
			<div className={"game"}>
				{(() => {
					const GameComponent = Games[gameName].Component;
					return <GameComponent mediaQuery={mediaQuery} virtualKey={virtualKey} chessBoardSize={width < 560 ? width : 560} willExit={newGameName !== gameName} onExit={() => setExitCount(c => c + 1)} clickedSquare={clickedSquare} showNotation={showNotation} flip={flip} />
				})()}
        		<BackgroundChessboard chessBoardSize={width < 560 ? width : 560} onSquareClick={square => setClickedSquare(square)} showBoardNotation={showNotation} flip={flip} />
			</div>
			{mediaQuery && <SettingsDesktop onFlipChange={() => setFlip(!flip)} onShowNotationChange={() => setShowNotation(!showNotation)} flip={flip} showNotation={showNotation} /> }
			{!mediaQuery && gameName === Games.Type.name && <VirtualKeyboard willExit={gameName !== newGameName} onExit={() => setExitCount(c => c + 1)} onKeyPress={k => setVirtualKey(k)} /> }
		</React.Fragment>
	);
}

export default App;