import React, { useState, useEffect, useRef } from 'react';

import { Chessboard } from "react-chessboard";
import useLichess from './useLichess';

const BackgroundChessboard = props => {
    
    const styleDiv = useRef(null);
    const move = useLichess(styleDiv);

    return (
        <div ref={styleDiv}>
            <Chessboard
                boardWidth={props.chessBoardSize}
                position={move}
                onSquareClick={props.onSquareClick}
                showBoardNotation={props.showBoardNotation}
                boardOrientation={props.flip ? "black" : "white"}
                arePiecesDraggable={false}
            />
        </div>
    );
}

export default BackgroundChessboard;