import React, { useState } from 'react';
import IconButton from '@mui/material/IconButton';
import SettingsIcon from '@mui/icons-material/Settings';
import Menu from './GameMenu/Menu';
import MenuItem from './GameMenu/MenuItem';

import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import FlipIcon from '@mui/icons-material/Flip';

import { gameIcon } from './GameMenu/style';

const iconButtonStyle = {
    float: "right",
    marginTop: "0.5em",
    marginRight: "0.5em",
    marginBottom: "0.5em"
};

const SettingsMobile = props => {

    const [anchor, setAnchor] = useState(null);

    return (
        <React.Fragment>
            <IconButton sx={iconButtonStyle} color="primary" onClick={e => setAnchor(e.currentTarget)}><SettingsIcon/></IconButton>
            <Menu anchorEl={anchor} open={Boolean(anchor)} onClose={() => setAnchor(null)}>
                <MenuItem onClick={props.onShowNotationChange}>
                    {props.showNotation ? 
                        [<VisibilityOffIcon key={0} sx={gameIcon} color="primary" fontSize="large" />, "Hide notation"]
                        : [<VisibilityIcon key={0} sx={gameIcon} color="primary" fontSize="large" />, "Show notation"] }
                </MenuItem>
                <MenuItem onClick={props.onFlipChange}>
                    <FlipIcon sx={gameIcon} color="primary" fontSize="large" /> Flip board
                </MenuItem>
            </Menu>
        </React.Fragment>
    );
}

export default SettingsMobile;
